import React,{useEffect} from "react";
import "./contact-page.css";
import $ from 'jquery';
function ContactPage() {
  useEffect(() => {
    $('.response').addClass("d-none");
    // eslint-disable-next-line
  }, [])
  
  const uri = `${process.env.REACT_APP_API_URL}`
  function displayResponse(responseText){
    $(".responsespan")[0].innerText = responseText

    $('.response').removeClass('d-none')
    $('.response').addClass('d-flex trans-res-anime')
    
    setTimeout(() => {
      $('.response').removeClass('d-flex trans-res-anime')
      $('.response').addClass('d-none')
    }, 2000);
  }
  async function sendFeedbk(e){
    e.preventDefault();
    const formData ={
      name: e.target.form[0].value,
      email:e.target.form[1].value,
      msg:e.target.form[2].value
    }
    const emailFormat = "/^\w+([\.-]?\w+)*@\w+([\.-]?\w+)*(\.\w{2,3})+$/"
    if(formData.email.match(emailFormat)){
      alert("Please enter a valid email")
      return;
    }
    try{
      const res = await fetch(`${uri}/feedbacks`,{
        method:'POST',
        headers:{
          'Content-Type':'application/json'
        },
        body:JSON.stringify(formData)
      });

      const dat = await res.json()
      if(dat.message==="Data is posted"){
          displayResponse("Thanks for your feedback! We will get back to you soon...");
      }

    }catch(e){
      console.log(e)
      displayResponse("Something went wrong...Try again later..");
    }
  }
  return (
    <>
      <div id="contact-page" className="contact-page">
        <form  method="POST">
          <div className="form-group">
            <label htmlFor="name">
              Your name<span> *</span>
            </label>
            <input
              type="text"
              className="form-control"
              id="name"
              name = 'name'
              placeholder=""
              required
            />
          </div>
          <div className="form-group">
            <label htmlFor="email">
              Your email<span> *</span>
            </label>
            <input
              type="email"
              className="form-control"
              id="email"
              name = 'email'
              aria-describedby="emailHelp"
              placeholder=""
              required
            />
            <small id="emailHelp" className="form-text text-muted">
              We'll never share your email with anyone else.
            </small>
          </div>
          <div className="form-group">
            <label htmlFor="msg">
              Your message<span> *</span>
            </label>
            <textarea
              rows="4"
              type="text"
              className="form-control"
              id="msg"
              name="msg"
              placeholder="Please write your message here.."
              required
            ></textarea>
          </div>
          <button type="submit" onClick={sendFeedbk} className="btn btn-color">
            Submit
          </button>
        </form>
      </div>
      <div className={`response`}>
        
        <span className="responsespan"> </span>
      </div>
    </>
  );
}

export default ContactPage;