import React from 'react'
import 'bootstrap/dist/css/bootstrap.min.css';
import 'bootstrap/dist/js/bootstrap.bundle.min';
import './display.css'
function DisplayCard(props) {
  return (
    <div className="cards" onClick={()=>{props.show(props)}} id="A" data-bs-toggle="modal" data-bs-target="#cardDiv">
    <div className="img-div">
      <img className="" src={`${props.pictures[0]}`} alt="Card cap" /> {/*[src]="site_obj.pictures[0]"*/}
    </div>
    <div className="overlay-div">
      <h5>{props.name}</h5>
    </div>
</div>

  )
}

export default DisplayCard