import React , {useState} from 'react'
import 'bootstrap/dist/css/bootstrap.min.css';
import 'bootstrap/dist/js/bootstrap.bundle.min';
import './Projects.css';
import DisplayCard from './DisplayCard/DisplayCard';
function ProjectsPage() {
  let disarray=[{
    id: 0,
    s_name: 'Mr Rajendra house',
    pictures: ['assets/media/Project/project_Display/mr_rajinder/pic1.jpg'],
    info: 'this is house of Mr Rajendra'
  },{
    id: 1,
    s_name: 'Mr Binod Gupta house',
    pictures: ['assets/media/Project/project_Display/mr_binod/pic1.jpg',
    'assets/media/Project/project_Display/mr_binod/pic2.jpg',
    'assets/media/Project/project_Display/mr_binod/pic3.jpg'],
    info: 'this is house of Mr binod gupta'
  },{
    id: 3,
    s_name: 'Mr Sunil house',
    pictures: ['assets/media/Project/project_Display/mr_sunil/pic1.jpg',
    'assets/media/Project/project_Display/mr_sunil/pic2.jpg',
    'assets/media/Project/project_Display/mr_sunil/pic3.png',
    'assets/media/Project/project_Display/mr_sunil/pic4.jpg'],
    info: 'this is house of Mr Sunil'
  },{
    id: 4,
    s_name: 'Mr Suri house',
    pictures: ['assets/media/Project/project_Display/mr_suri/pic4.jpg','assets/media/Project/project_Display/mr_suri/pic1.jpg',
    'assets/media/Project/project_Display/mr_suri/pic2.jpg',
    'assets/media/Project/project_Display/mr_suri/pic3.jpg',
    'assets/media/Project/project_Display/mr_suri/pic4.jpg',
    'assets/media/Project/project_Display/mr_suri/pic5.jpg',
    'assets/media/Project/project_Display/mr_suri/pic6.jpg'],
    info: 'this is house of mr suri'
  },{
    id: 5,
    s_name: 'Mr Walia house',
    pictures: ['assets/media/Project/project_Display/mr_walia/pic1.jpg',
    'assets/media/Project/project_Display/mr_walia/pic2.jpg',
    'assets/media/Project/project_Display/mr_walia/pic3.jpg'],
    info: 'this is house of C'
  },{
    id: 6,
    s_name: 'Naraingarh Site',
    pictures: ['assets/media/Project/project_Display/naraingarh_site/pic1.jpg'],
    info: 'this is our site in naraingarh'
  },{
    id: 7,
    s_name: 'Temple Project',
    pictures: ['assets/media/Project/project_Display/ravi_das_temple/pic1.jpg'],
    info: 'this is our temple project'
  },{
    id: 8 ,
    s_name: 'Usha ji\'s house',
    pictures: ['assets/media/Project/project_Display/usha_g/pic1.jpg',
    'assets/media/Project/project_Display/usha_g/pic2.jpg',
    'assets/media/Project/project_Display/usha_g/pic3.jpg',
    'assets/media/Project/project_Display/usha_g/pic4.jpg',
    'assets/media/Project/project_Display/usha_g/pic5.jpg',
    'assets/media/Project/project_Display/usha_g/pic6.jpg'],
    info: 'this is our project for usha ji\'s residence'
  }];
  

  const [title, settitle] = useState('')
  const [disImage, setdisImage] = useState([])
  function showdiv(e){
    settitle(e.name)
    setdisImage(e.pictures);
  }
  return (
    <div className="projects-window">
        <div className="projects-area" >
          {
            disarray.map((element)=>{
              return <DisplayCard show={showdiv} key={element.id} name={element.s_name} pictures={element.pictures} style={{"margin":"2% 0px 0px 1% "}} />
            })
          }
        </div>
        <div className="modal b-modal fade bd-example-modal-lg" tabIndex="-1" id="cardDiv" role="dialog">
            <div className="modal-dialog modal-lg">
                <div className="modal-content my-modal">
                    <h2>{title}</h2>
                    <div className="close-button">
                        <button type="button" className="close btn-close" data-bs-dismiss="modal" aria-label="Close">
                            <span aria-hidden="true">&times;</span>
                        </button>
                    </div>
                    <div className="imgDiv" >
                      {disImage.map(element => {
                        return <img src={`${element}`} alt="plan"></img>
                      })}
                    </div>
                </div>
            </div>
        </div>
    </div>
  )
}

export default ProjectsPage