import React from "react";
import "bootstrap/dist/css/bootstrap.min.css";
import "bootstrap/dist/js/bootstrap.bundle.min";
import "./Projects.css";
import { Link } from "react-router-dom";

export default function Projects() {
  return (
    <div id="proj-win" className="projects d-flex justify-content-center align-items-center ">
      <div className="wrappers">
        <div className="desc-section sec2
        ">
          <h1>Projects</h1>
          <p> We have experiences in building from shops to big 
            institutions and designing Houses to Big Mansions. Feel free to explore some of our projects. </p>
            <Link to="/projects" type="button" className="btn btnd btn-outline-dark">
          Explore Our Projects 
        </Link>
        </div>
        <div className="desc-section
        ">
          <h1>Services</h1>
          <div id="services">
            <div className="service">
              <h3>Architecture</h3>
              <p>We provide you the best 3D designs and
                constructional plans that helps to not
                only see your dream house on paper but also
                to make these illustrations designs real.
                We have expertise in Residential as well as commercial
                drawings.
              </p>
            </div>
            <div className="service">
              <h3>Interior Design</h3>
              <p>As world is advancing it has become accessible to
                explore our imaginative world using softwares.
                So, we use latest rendering softwares to make the 
                your dream interior you want live infront of you.
              </p>
            </div>
            <div className="service">
              <h3>Construction</h3>
              <p>We also provide service for construction we get along 
                with you from foundation to Inaugration and supervise 
                the constructional work for you.
              </p>
            </div>
          </div>
        </div>
      </div>
    </div>
  );
}