import React,{useEffect,useState} from 'react';
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome'
import {brands} from '@fortawesome/fontawesome-svg-core/import.macro' // <-- import styles to be used
 
import './Intro.css';
function Intro() {
  const [quote, setQuote] = useState({});
  useEffect(() => {
    const Convey = async()=>{
      const uri = `${process.env.REACT_APP_API_URL}`
      const num = Math.floor(Math.random()*10); 
      let resp =  await fetch(`${uri}/quotes/${num+1}`);
      let respObj = await resp.json();
      setQuote(respObj)
    }
    if(document.readyState==='complete'){
      Convey();
    }else{
      window.addEventListener('load',Convey)
      return () => window.removeEventListener('load', Convey);
    }
  }, [])
  return (
    <div id="Home" className="Intro-box  ">
        <div className='quote'><p >{quote.content}<br></br><span style={{"color":" rgb(231, 123, 84)"}} >~ {quote.author}</span></p></div>
        <img src="../../../assets/media/introPic/pic7.jpg" alt="house"></img>

        <span className="hello d-inline">Follow us on : </span><a href="https://www.facebook.com/archiritiarchitects" target="_blank" >
        <FontAwesomeIcon icon={brands('facebook')} className='social'/>
        {/* <FontAwesomeIcon icon="fa-brands fa-facebook" /> */}
        </a>
    </div>
  )
}

export default Intro